/* 기존 스타일 유지 */
.centered-name {
  border: 1px solid black;
  /* 투명한 테두리 설정 */
  display: flex;
  flex-direction: column;
  /* 세로 방향으로 배치 */
  justify-content: center;
  align-items: center;
  position: relative;
  /* action-value 위치 조정을 위해 */
}

/* actionValue 스타일 */
.action-value {
  left: 0;
  right: 0;
  text-align: center;
}

.action-value.has-value {
  /* 값이 있을 때 배경색 변경 */
  background-color: rgb(50, 89, 196);
  /* 원하는 배경색으로 변경 */
  color: white;
  width: 100%;
  /* 넓이 100%로 설정 */
}

.centered-name.selected {
  /* 선택된 경우에 적용될 스타일 */
  border-image: linear-gradient(to right, red, blue);
  /* 그라데이션 테두리 이미지 설정 */
  border-image-slice: 1;
  /* 테두리 이미지를 균일하게 분할 */
  animation: border-animation 3s linear infinite;
  /* 애니메이션 설정 */
  border-width: 3px;
}

@keyframes border-animation {
  0% {
    border-image-source: linear-gradient(to right, rgb(50, 50, 244), rgb(249, 41, 41));
  }

  50% {
    border-image-source: linear-gradient(to right, rgb(249, 41, 41), rgb(50, 50, 244));
  }

  100% {
    border-image-source: linear-gradient(to right, rgb(50, 50, 244), rgb(249, 41, 41));
  }
}

.animated-text {
  overflow: hidden;
  white-space: nowrap;
}

/* selected 클래스가 적용된 경우에만 애니메이션 적용 */
.centered-name.selected .animated-text {
  animation: text-animation 5s linear infinite;
}

@keyframes text-animation {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

.centered-name.selected .action-value.has-value {
  /* 선택된 경우에만 애니메이션 적용 */
  background-color: rgb(255, 98, 0);
  /* 변경될 배경색 */
}